@import  (less) 'color';

.truncate {
  display:inline-block;
  width: 2em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.pagination-container {
    margin-top: 0.625em;
    table {
        width: 100%;
        table-layout: fixed;
    }
    .table-page-btn {
        display: inline-block;
        color: @green;
        cursor: pointer;
        margin-top: 0.313em;
        background-color: transparent;
        border: none;
        &.disabled {
            color: @gray;
            cursor: auto;

        }
        .page-btn-arrow {
            font-size: 1.250em;
            vertical-align: middle;
        }
        .page-btn-text {
            padding: 0 0.625em 0 0.625em;
            font-size: 0.813em;
            font-weight: bold;
        }
         .table-page-number {
            display: inline-block;
            border: 1px solid @green;
            font-weight: bold;
            color: @green;
            background: @white;
            text-align: center;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            width: 2em;
            height: 2em;
            font-size: 0.938em;
            line-height: 2em;
            margin: 0 0.125em 0 0.125em;
            
            &.backward {
                margin-right: -0.125em;
            }
            &.forward {
                margin-left: -0.125em;
            }
        }
        .table-page-number.active {
            color: @white;
            background: @green;
            cursor:default;
        }
    }
    .pagination-container-center {
        text-align: center;
        .table-page-btn {
            margin-top: 0;
        }
    }
}

span.table-page-number[tooltip]:hover {
  position: relative;
}

span.table-page-number[tooltip]:hover:after {
  content: attr(tooltip);
  padding: 4px 8px;
  color: @green;
  position: absolute;
  left: 0;
  top: 100%;
  white-space: nowrap;
  z-index: 1000;
  border-radius: 5px;
  background-image:transparent;
}


.contrasto-on {

    .pagination-container {
        .table-page-btn {
            color: @black;
            &.disabled {
                color: @gray;
            }
            .table-page-number {
                border: 1px solid @black;
                color: @black;
                background: @white;
            }
            .table-page-number.active {
                color: @white;
                background: @black;
            }
        }
    }
}